.fse-season-label {
  display: grid; }

.fse-season-label-year {
  font-weight: 700; }

.fse-events-loop .fse-season-label {
  grid-column: var(--fse-column-cards-column-column); }

@media (min-width: 64rem) {
  .fse-season-label {
    text-align: right; } }
